* {
    font-family: 'Bellefair';
}
button {
    cursor: pointer;
}
body{
    background-color: antiquewhite;
}
h5{
    margin: 0;
    padding: 0;
    font-size: 17px;
}
#root{
    background-color: antiquewhite;
}
#main{
    background-color: antiquewhite;
    height: 100%;
}

#heading{
    height: 100px;
    display: grid;
    grid-template-columns: 10vw 80vw 10vw;
    flex-direction: row;
    justify-items: center;
    align-items: center;
}
@import url('https://fonts.googleapis.com/css?family=Bellefair');
@import url('https://fonts.googleapis.com/css?family=Prata');

#title {
    font-size: 40px;
    display: block;
}
#scoreBoard{
    display: flex;
    color: black;
    border-radius: 10px;
    float: right;
    padding: 10px;
    width: 89px;
    flex-direction: column;
    justify-content: space-evenly;
    gap: 10px;
}
#grid {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(210px, 1fr));
    grid-template-rows: repeat(auto-fit, minmax(260px, 1fr));
    gap: 20px;
    justify-items: center;
    align-items: center;
    padding: 0px 2vw;
}

.tile {
    display: flex;
    flex-direction: column;
    align-items: center;
    overflow: hidden;
    width: 200px;
    height: 250px;
    border-radius: 50px;
    border: 2px solid rgb(70, 55, 45);
}
.tile:hover {
    cursor: pointer;
}
.tileImages{
    width: 100%;
    height: 100%;
}

#modalShadow {
    display: flex;
    position: fixed;
    z-index: 1;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    overflow: auto; /* Enable scroll if needed */
    background-color: rgb(0,0,0); /* Fallback color */
    background-color: rgba(0,0,0,0.7); /* Black w/ opacity */
    flex-direction: column;
    align-content: center;
    align-items: center;
    justify-content: center;
}

.modal {
    background-color: antiquewhite;
    padding: 20px;
    border-radius: 20px;
    width: 40vw;
    box-shadow: 5px 5px 16px 1px black;
}
.modalCenter {
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}
.close{
    float: right;
    background-color: antiquewhite;
    border: 1px solid rgb(70, 55, 45);
    border-radius: 20px;
    padding: 7px 10px;
}
.modalButton{
    background-color: antiquewhite;
    border: 1px solid rgb(70, 55, 45);
    border-radius: 20px;
    padding: 7px 10px;
}
.close:hover {
    background-color: #da8379;
    border: none;
    padding: 8px 11px;
}
.modalButton:hover {
    background-color: rgb(123, 168, 123);
    border: none;
    padding: 8px 11px;
}

#help {
    background-color: antiquewhite;
    font-size: 25px;
    border: 2px solid rgb(70, 55, 45);
    height: 40px;
    width: 40px;
    border-radius: 20px;
}
#help:hover {
    /* border: none; */
    height: 38px;
    width: 38px;
    border: none;
    background-color: #da8379;
    box-shadow: 1px 1px 2px 1px black;
}